html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0; }

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5; }

p {
  color: #000000;
  font-size: 2rem; }

.container {
  width: 80%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

i {
  padding: 2px; }

li {
  list-style: none; }

img,
picture {
  width: 100%;
  display: block; }

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 1rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.5rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.5rem; }

.title {
  font-weight: bold;
  color: #04e762; }

.selected {
  font-weight: 700;
  color: #b24b8a; }

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

.align-rigth {
  display: flex;
  justify-content: flex-end; }

.align-left {
  display: flex;
  justify-content: flex-start; }

.green-btn {
  background: #04e762;
  margin-top: 2rem;
  color: #ffffff;
  text-align: center;
  padding: 0.5rem 3rem;
  display: block; }
  .green-btn:hover {
    background: #04ce57; }

.black-btn {
  background: #000000;
  margin-top: 2rem;
  color: #ffffff;
  text-align: center;
  padding: 0.5rem 3rem;
  display: inline-block; }
  .black-btn:hover {
    background: black; }

.no-webp .home {
  background-image: url("/build/img/header.jpg"); }

.webp .home {
  background-image: url("/build/img/header.webp");
  object-fit: fill;
  background-repeat: no-repeat;
  background-size: cover; }

.header {
  background: #04e762;
  padding: 3rem 2rem; }
  @media (min-width: 768px) {
    .header.home {
      min-height: 70rem; } }
  .header.home .header-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 60rem; }

.header-title {
  color: #ffffff;
  text-align: left;
  font-weight: 400; }

@media (min-width: 768px) {
  .about {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem; } }

.about-content {
  margin-bottom: 4rem;
  text-align: center;
  font-size: 1.8px; }
  .about-content img {
    height: 10rem; }
  .about-content h3 {
    text-transform: uppercase; }

@media (min-width: 768px) {
  .property-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem; } }

@media (min-width: 1024px) {
  .property-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem; } }

.property {
  background: #eef0f2;
  align-items: center; }
  .property .property-content {
    padding: 2rem;
    text-align: center; }
  .property .description {
    font-size: 1.6rem; }
  .property .area {
    font-size: 1.5rem;
    color: #272712; }

.property-icons {
  margin-top: 2rem;
  display: flex;
  justify-content: space-around; }
  .property-icons img {
    width: 2rem; }
  .property-icons li {
    width: 40px;
    display: flex;
    justify-content: space-between; }

.footer {
  background: #04e762;
  padding: 2.5rem 0; }
  .footer .footer-content {
    padding: 2.5rem 0; }
  .footer .copyright {
    color: #ffffff;
    text-align: center; }

.nav {
  display: flex;
  flex-direction: column; }
  @media (min-width: 768px) {
    .nav {
      flex-direction: row;
      justify-content: space-between; } }
  .nav .logo {
    color: #ffffff;
    font-size: 40px;
    font-weight: 700; }

@media (min-width: 768px) {
  .nav-content {
    display: flex;
    align-items: center; } }

.nav-content.home-nav a:hover {
  color: #04e762; }

.nav-content a {
  color: #ffffff;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: left; }
  @media (min-width: 768px) {
    .nav-content a {
      margin-right: 2rem;
      font-size: 1.8rem; }
      .nav-content a:last-of-type {
        margin-right: 0; } }
